import { Component, OnInit } from "@angular/core";
import { Router } from  "@angular/router";

import { UserService } from "../_services/user.service";
import { FileService } from "../_services/file.service";

@Component({
    moduleId: module.id,
    templateUrl: 'source-files.component.html',
    styleUrls: ['source-files.component.css'],
    animations: [

    ]
})
export class SourceFilesComponent  implements OnInit{
    nameArray: any = [];
    constructor(
        private userService: UserService,
        private fileService: FileService,
        private router: Router,
    ){}
    ngOnInit(){
        var id: string = JSON.parse(localStorage.getItem('user'));
        console.log(this.userService.getUserFiles(id));
        this.userService.getAllUserFiles(id).subscribe(data => {
            this.nameArray = data;
            this.nameArray.reverse();
        });
    }
    openSourceFile(filename: string){
        this.fileService.uploadFromSide(filename);
        this.router.navigate(['/dashboard']);
    }
}
